.tile {
    height: 4rem;
    width: 4rem;
    border: 2px solid #000000;
    background-color: #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tile.visited {
    background-color: #efefef;
}

.tile img {
    max-width: 100%;
    max-height: 100%;
}