.board {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.board .row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.board-container .controls {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
}

.board-container .status {
    color: azure;
    text-align: center;
}

.board-container .title {
    color: azure;
    text-align: center;
}

.board-container .message {
    color: azure;
    text-align: center;
}